import React from 'react';
import { extend } from 'koot';

import navItems from '@constants/nav-items';
import { github as urlGithub } from '@constants/urls';

import NavItem from '@components/nav/item';
import Center from '@components/center';
import Icon from '@components/icon';

import styles from './index.module.less';
import { Link, IndexLink } from 'react-router';

// ============================================================================
@extend({
    styles: require('./index.module.less'),
    
})
class Nav extends React.Component {
    constructor(props){
        super(props);
        this.changeList = this.changeList.bind(this);
        this.state={
            listState : 'none'
        }
    }
    changeList(){
        let dis = this.state.listState=='none'?'block':'none';
        this.setState({
            listState:dis
        })
    }
    componentDidMount(){
        let _this = this;
        document.addEventListener('click',function(event){
            console.log(event.target.className);
            if(event.target.className=='button')return;
            let dis = _this.state.listState=='none'?'block':'none';
            _this.setState({
                listState:dis
            })
        })
    }
    render() {
        const { className } = this.props;
        return (
            <nav className={className}>
            <div className="nav">
            <div className="logo">
                <Link to="/" className="home">Ho's Entertainment Corporation Limited</Link>
                </div>
            <div className="protocol">
                <div className="button" onClick={this.changeList}>Protocol</div>
                
            </div>
            </div>
            <div className="nav_list" style={{display:this.state.listState}}>
                <Link to='/policy' className="item">Privacy Policy</Link>
                <Link to='/service' className="item">Terms of Service</Link>
            </div>
        </nav>
        );
    }
}

export default Nav;

